import React from 'react';

import profileImage from '../../image/Profile.png';

import './index.scss';

const About = (props) => {

    return (
        <div className='contentsbox' id='about'>
            <h2>About</h2>
            <div className='about-content'>
                <div className='profile'>
                    <img className='profile-image' src={profileImage} alt='No Image' width='177' height='236' />
                    <h3>김윤지 ( Yunji Kim )</h3>
                </div>
                <div className='introduce'>
                    <p>대학교 복학 직후 수강한 Java 강의에서, 교수님께서 ‘학기말까지 GUI로 게임을 만들어오면 A+ 주겠다.’ 는 말씀을 하셨습니다. 그 말을 듣고 매일 Java를 공부하여, 3주 뒤에 2048 게임을 구현하였습니다. 성적 때문에 시작한 토이 프로젝트였으나, Java를 공부하고 만들어가는 과정에서 재미를, 결과물을 보면서 성취감을 느꼈습니다. 지뢰찾기와 팩맨을 추가로 구현해 보았고, 이를 계기로 개발자가 되기로 결심했습니다.</p>
                    <p>대학 교과 과정 외 기술을 배워보고 싶다고 생각 할 때쯤, 지인의 ‘React 커리큘럼을 만들어봤는데 경험해봐라’ 는 말에 React를 접하게 되었습니다. 그 후 교과 프로젝트들의 클라이언트를 React로 개발하며, 웹 개발의 길을 걷게 되었습니다.</p>
                    <p>저는 현재에 만족하지 않고 지속적으로 발전하며, 당당하게 자신을 ‘개발자’라 소개할 수 있는 그런 개발자가 되고 싶습니다.</p>
                </div>
            </div>
        </div>
    );
}

export default About;